.search-container {
	text-align: left;
    z-index: 1;
    position: relative;
    max-width: 100%;
}
.search-container.hidden {
	display: none;
}
.search-container #search-box {
	font-family: inherit;
	box-sizing: border-box;
	height: 2rem;
	font-size: 1rem;
	border-radius: 3px;
	border: 1px solid rgba(0,0,0,0.2);
	padding-left: 0.25rem;
	margin: 0 0 5px 0;
	width: 100%;
}

.search-container .search-list {
	display: none;
	margin-top: 1px;
	min-width: 100%;
	max-width: calc(100% + 2rem);
	max-height: 50vh;
	overflow: auto;
    position: absolute;
}
.search-container.active .search-list {
	display: block;
}
.search-container .search-list .search-item {
	height: auto;
	padding: 0.5rem;
	cursor: pointer;
	position: relative;
}
.search-container .search-list .search-item:hover, .search-container .search-list .search-item.selected {
    opacity: 0.8;
}
.search-container .search-list .search-item p {
	margin: 0;
	display: block;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	pointer-events: none;
}