/* Styles go in this file. */
@import "./search";

html, body { height: 100%; margin: 0; -webkit-font-smoothing: antialiased; }
.fl-legend-title:empty {
	margin: 0;
}
footer {
	.flourish-footer-note:empty {
		margin: 0;
	}
}
#table-container {
	position: relative;
	overflow: hidden;
	height: 100%;
}

#table-outer {
	overflow: auto;
	height: 100%;
}

#table-inner {
	display: table;
	width: 100%;
	height: 100%;
	border-collapse: collapse;
	box-sizing: border-box;
	#header-row {
		font-weight: bold;
		.td.sorting.sorted {
			position: relative;
			&:before {
				position: absolute;
				right: 5px;
				display: block;
				font-size: 12px;
				top: 50%;
				transform: translateY(-50%);
			}
			&.ascending:before {
				content: "▲";
			}
			&.descending:before {
				content: "▼";
			}
		}
	}
	.tr {
		display: table-row;
	}
	.td {
		display: table-cell;
		border: none;
		overflow: hidden;
		cursor: default;
		line-height: 1.25em;
		position: relative;
		vertical-align: top;
		.cell-header {
			display: none;
			> * {
				margin: 0;
				display: inline-block;
			}
		}
		svg { display: block; }
		img {
			max-height: 100%;
			display: block;
		}
		.cell-body {
			display: flex;
			& > * {
				margin: 0;
				display: inline-block;
			}
			p {
				display: inline-block;
			}
		}
	}
	&.hidden {
		display: none;
	}
}

[dir=rtl] #table-inner #header-row .td.sorting.sorted:before {
	right: auto;
	left: 5px;
}

.cell-popup {
	& > * {
		margin: 0;
		display: inline-block;
	}
}

.flourish-popup-content {
	pointer-events: none !important;
	* {
		margin: 0;
	}
}

#edge-horizontal {
    width: 1px;
    position: absolute;
    height: 100%;
    top: 0;
    right: 0;
    box-shadow: -1px 0px 4px rgba(0,0,0,0.3);
}

[dir=rtl] #edge-horizontal {
	left: 0;
	right: auto;
	box-shadow: 1px 0px 4px rgba(0,0,0,0.3);
}

#edge-vertical {
    width: 100%;
    position: absolute;
    height: 1px;
    bottom: 0;
	right: 0;
	left: 0;
    box-shadow: 0px -1px 4px rgba(0,0,0,0.3);
}

#pagination {
	text-align: center;
	font-size: 14px;
	* { display: inline-block }
	.pagination-btn {
		background: #888;
		color: #fff;
		font-family: sans-serif;
		border: none;
		font-size: 14px;
		font-weight: bold;
		border-radius: 3px;
		height: 24px;
		width: 21px;
		cursor: pointer;
		&.disabled {
			pointer-events: none;
			opacity: 0.2;
		}
	}
	input {
		height: 24px;
		box-sizing: border-box;
		font-size: 14px;
		font-family: inherit;
		width: 30px;
		text-align: center;
	}
	p {
		margin: 10px 20px;
	}
}

#table-inner.blocks {
	#header-row { display: none };
	.tr {
		padding: 10px 0;
	}
	.tr, .td { display: block; height: auto !important; };
	.td .cell-header {
		display: block;
		font-weight: bold;
	}
}

#no-results {
	text-align: center;
	margin: 1em 0;
	opacity: 0.5;
	&.hidden {
		display: none;
	}
}